import React, { Component } from "react";
import ChatBot from "react-simple-chatbot";
import Movierecomendation from "./movieRecomendation/movieRecomendation";
import { ThemeProvider } from "styled-components";

const theme = {
  background: "#f5f8fb",
  //fontFamily: 'Helvetica Neue',
  headerBgColor: "black",
  headerFontColor: "#fff",
  headerFontSize: "15px",
  botBubbleColor: "black",
  botFontColor: "#fff",
  userBubbleColor: "#fff",
  userFontColor: "#4a4a4a",
};
class Moviebot extends Component {
  render() {
    const config = {
      width: "400px",
      height: "500px",
      floating: true,
      floatingStyle: {
        bottom: "72px",
        border: "1px solid white" 
      }
    };
    return (
      <ThemeProvider theme={theme}>
        <ChatBot
          steps={[
            {
              id: "first-message",
              message: "",
              trigger: "second-message",
            },
            {
              id: "second-message",
              message: " - ?",
              trigger: "advice",
            },
            {
              id: "advice",
              options: [
                { value: "y", label: "", trigger: "recomendation" },
                { value: "n", label: "", trigger: "no-submit" },
              ],
            },
            {
              id: "recomendation",
              message: "   ?",
              trigger: "recomendationList",
            },
            {
              id: "recomendationList",
              options: [
                {
                  value: 2,
                  label: "",
                  trigger: "recomendation-message",
                },
                { value: 3, label: "", trigger: "recomendation-message" },
                {
                  value: 4,
                  label: "",
                  trigger: "recomendation-message",
                },
                {
                  value: 6,
                  label: ``,
                  trigger: "recomendation-message",
                },
                {
                  value: 16,
                  label: "",
                  trigger: "recomendation-message",
                },
                {
                  value: 17,
                  label: "",
                  trigger: "recomendation-message",
                },
                {
                  value: 4,
                  label: "",
                  trigger: "recomendation-message",
                },
                { value: 8, label: "", trigger: "recomendation-message" },
                {
                  value: 10,
                  label: "",
                  trigger: "recomendation-message",
                },
              ],
            },
            {
              id: "recomendation-message",
              message: "  ?",
              trigger: "recomendation-movie",
            },
            {
              id: "recomendation-movie",
              component: <Movierecomendation />,
              asMessage: true,
              trigger: "end-message",
            },
            {
              id: "no-submit",
              message: " .",
              trigger: "end-message",
            },
            {
              id: "end-message",
              options: [
                {
                  value: "n",
                  label: "  ",
                  trigger: "second-message",
                },
              ],
            },
          ]}
          {...config}
        />
      </ThemeProvider>
    );
  }
}

export default Moviebot;
